'use strict';

layui.define(function (exports) {
    exports('renderNav', function (strData) {
        var data = typeof strData === "string" ? JSON.parse(strData) : strData;

        var ulHtml = '<ul class="layui-nav layui-nav-tree">';
        for (var i = 0; i < data.length; i++) {
            ulHtml += '<li class="layui-nav-item ' + (data[i].spread ? 'layui-nav-itemed' : '') + '">';
            if (data[i].children !== undefined && data[i].children.length > 0) {
                ulHtml += '<a href="javascript:;">';
                if (data[i].icon) {
                    ulHtml += '<i class="iconfont-jl icon-jl-' + data[i].icon + '"></i>';
                }
                ulHtml += '<cite>' + data[i].title + '</cite>';
                ulHtml += '<span class="layui-nav-more"></span>';
                ulHtml += '</a>';
                ulHtml += '<dl class="layui-nav-child">';
                for (var j = 0; j < data[i].children.length; j++) {
                    if (data[i].children[j].target === "_blank") {
                        ulHtml += '<dd><a href="javascript:;" data-url="' + data[i].children[j].href + '" target="' + data[i].children[j].target + '">';
                    } else {
                        ulHtml += '<dd><a href="javascript:;" data-url="' + data[i].children[j].href + '">';
                    }
                    if (data[i].children[j].icon) {
                        ulHtml += '<i class="iconfont-jl icon-jl-' + data[i].children[j].icon + '"></i>';
                    }
                    ulHtml += '<cite>' + data[i].children[j].title + '</cite></a></dd>';
                }
                ulHtml += "</dl>";
            } else {
                if (data[i].target === "_blank") {
                    ulHtml += '<a href="javascript:;" data-url="' + data[i].href + '" target="' + data[i].target + '">';
                } else {
                    ulHtml += '<a href="javascript:;" data-url="' + data[i].href + '">';
                }
                if (data[i].icon) {
                    ulHtml += '<i class="iconfont-jl icon-jl-' + data[i].icon + '"></i>';
                }
                ulHtml += '<cite>' + data[i].title + '</cite></a>';
            }
            ulHtml += '</li>';
        }
        ulHtml += '</ul>';
        return ulHtml;
    });
});